import {Card, CardContent, CardActions,CssBaseline, Container , Typography} from '@material-ui/core';
import React , {useEffect, useState} from 'react';

function App() {
  const [jokes,setJokes] = useState([])

  useEffect(() => {
    // fetch('https://api.chucknorris.io/jokes/random')
      fetch('https://api.chucknorris.io/jokes/random')
      .then((res) => res.json())
      .then((data) => {
         
        setJokes((s) => [...s, data.value])
        
      }, [])
      .catch((err) => console.log(err));
       
  }) 
  
  return (
    <div className="App">
      <CssBaseline />
      <Container>
      <Typography variant = "h1" align = "center" >
         Some jokes
         </Typography>
          
           <Card key = {jokes.id}>
             <CardContent>
               
               {jokes.map((joke, index)=> (
                 
                 <h1 key = {index}>{joke.category }</h1>
               ))}
               
               <Typography>{jokes.map((joke, index)=> (
                 
                 <li key = {index}>{joke }</li>
               ))}</Typography>
             </CardContent>

           </Card>
         

      </Container>
      
    </div>
  );
}

export default App;
